import { createDict } from '@ali/4ever-utils';

const englishQuotes = createDict({
  '\'': true,
  '"': true,
  '`': true,
});
const chineseQuotes = createDict({
  '“': true,
  '”': true,
  '‘': true,
  '’': true,
});
const chineseLeftBrackets = createDict({
  '【': true,
  '（': true,
});
const chineseRightBrackets = createDict({
  '】': true,
  '）': true,
});
const englishLeftBrackets = createDict({
  '{': true,
  '[': true,
  '(': true,
});
const englishRightBrackets = createDict({
  '}': true,
  ']': true,
  ')': true,
});
const rightBrackets = createDict({
  '}': true,
  ']': true,
  ')': true,
  '】': true,
  '）': true,
});
const coupleKey = createDict({
  ...englishLeftBrackets,
  ...englishRightBrackets,
  ...englishQuotes,
  ...chineseLeftBrackets,
  ...chineseRightBrackets,
  ...chineseQuotes,
});
const leftCoupleKey = createDict({
  ...englishLeftBrackets,
  ...chineseLeftBrackets,
  ...englishQuotes,
});
const coupleMap = createDict({
  '\'': '\'',
  '"': '"',
  '`': '`',
  '{': '}',
  '[': ']',
  '(': ')',
  '【': '】',
  '（': '）',
});
const couplePair = createDict({
  '\'\'': true,
  '""': true,
  '``': true,
  '{}': true,
  '[]': true,
  '()': true,
  '【】': true,
  '（）': true,
  '“”': true,
  '‘’': true,
});
const chineseQuotePair = createDict({
  '‘': ['‘', '’'],
  '’': ['‘', '’'],
  '“': ['“', '”'],
  '”': ['“', '”'],
});
export function isCoupleKey(key): boolean {
  return coupleKey[key];
}
export function isLeftCoupleKey(key): boolean {
  return leftCoupleKey[key];
}
export function isRightBracket(key): boolean {
  return rightBrackets[key];
}
export function getRightCoupleKey(key): string {
  return coupleMap[key];
}
export function isCouple(left, right): boolean {
  return couplePair[left + right];
}
export function isEnglishQuoteKey(key): boolean {
  return englishQuotes[key];
}
export function isChineseQuoteKey(key): boolean {
  return chineseQuotes[key];
}
export function getChineseQuoteCP(key): [string, string] {
  return chineseQuotePair[key];
}
